<template>
	<div class="main-content">

		<!--  -->
		<div v-if="showFlag">
			<el-form :inline="true" :model="searchForm" class="form-content">
				<el-row :gutter="20" class="slt" :style="{justifyContent:contents.searchBoxPosition=='1'?'flex-start':contents.searchBoxPosition=='2'?'center':'flex-end'}">

					<el-form-item :label="contents.inputTitle == 1 ? '' : ''">
						<el-input prefix-icon="el-icon-search" v-model="searchForm.shangpinOrderUuidNumber" placeholder="" clearable></el-input>
					</el-form-item>

					<el-form-item :label="contents.inputTitle == 1 ? '' : ''">
						<el-select v-model="searchForm.shangpinOrderTypes" placeholder="">
							<el-option label="=--=" value=""></el-option>
							<el-option v-for="(item,index) in shangpinOrderTypesSelectSearch" v-bind:key="index" :label="item.indexName"
							 :value="item.codeIndex">
								<!--lable-->
								<!--value-->
							</el-option>
						</el-select>
					</el-form-item>



					<el-form-item :label="contents.inputTitle == 1 ? '' : ''">
						<el-input prefix-icon="el-icon-search" v-model="searchForm.caigouyuanName" placeholder="" clearable></el-input>
					</el-form-item>

					<el-form-item :label="contents.inputTitle == 1 ? '' : ''">
						<el-input prefix-icon="el-icon-search" v-model="searchForm.shangpinName" placeholder="" clearable></el-input>
