							<el-button v-if="isAuth('shangpinOrder','')" type="success" icon="el-icon-tickets" size="mini" @click="addOrUpdateHandler(scope.row.id,'info')"></el-button>
							<el-button v-if="isAuth('shangpinOrder','')" type="primary" icon="el-icon-edit" size="mini" @click="addOrUpdateHandler(scope.row.id)"></el-button>

							<el-button v-if="isAuth('shangpinOrder','')" type="danger" icon="el-icon-delete" size="mini" @click="deleteHandler(scope.row.id)"></el-button>
							<el-button v-if="isAuth('shangpinOrder','') && scope.row.shangpinOrderTypes == 1 && sessionTable=='caigouyuan' && userId==scope.row.caigouyuanId"
