							<el-button v-if="isAuth('shangpinOrder','') && scope.row.shangpinOrderTypes == 3 && sessionTable=='caigouyuan' && userId==scope.row.caigouyuanId"
							 type="primary" icon="el-icon-reading" size="mini" @click="receiving(scope.row.id)"></el-button>

							<el-button v-if="isAuth('shangpinOrder','') && scope.row.shangpinOrderTypes == 4 && sessionTable=='yonghu' && userId==scope.row.yonghuId"
							 type="primary" icon="el-icon-reading" size="mini" @click="openCommentModel(scope.row.id)"></el-button>



						</template>
					</el-table-column>
				</el-table>
				<el-pagination clsss="pages" :layout="layouts" @size-change="sizeChangeHandle" @current-change="currentChangeHandle"
				 :current-page="pageIndex" :page-sizes="[10, 20, 50, 100]" :page-size="Number(contents.pageEachNum)" :total="totalPage"
				 :small="contents.pageStyle" class="pagination-content" :background="contents.pageBtnBG" :style="{textAlign:contents.pagePosition==1?'left':contents.pagePosition==2?'center':'right'}"></el-pagination>
			</div>
		</div>
		<!-- /  search-->
		<add-or-update v-if="addOrUpdateFlag" :parent="this" ref="addOrUpdate"></add-or-update>

		<!--  -->
		<el-dialog title="" :visible.sync="commentbackVisible" width="30%">
			<span></span>
			<el-input type="textarea" v-model="commentbackContent"></el-input>
			<span slot="footer" class="dialog-footer">
				<el-button @click="commentbackVisible = false"> </el-button>
				<el-button type="primary" @click="commentback()"> </el-button>
			</span>
		</el-dialog>

		<el-dialog title="" :visible.sync="chartVisiable" width="auto">
			<el-date-picker v-model="echartsDate" type="year" placeholder="">
			</el-date-picker>
			<el-button @click="chartDialog()"></el-button>
			<div id="statistic" style="width:100%;height:600px;"></div>
			<span slot="footer" class="dialog-footer">
				<el-button @click="chartVisiable = false"></el-button>
			</span>
		</el-dialog>


		<el-dialog title="" :visible.sync="dialogFormVisible">
			<el-form :model="fahuoDate">
				<el-select v-model="fahuoDate.caigouyuanId" placeholder="">
					<el-option v-for="(item,index) in caigouyuanSelectSearch" v-bind:key="index" :label="item.caigouyuanName" :value="item.id">
						<!--lable-->
						<!--value-->
					</el-option>
				</el-select>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogFormVisible = false"> </el-button>
				<el-button type="primary" @click="deliver"> </el-button>
			</div>
		</el-dialog>

	</div>
</template>
<script>
	import AddOrUpdate from "./add-or-update";
	import styleJs from "../../../utils/style.js";
	import utilsJs from "../../../utils/utils.js";

	export default {
		data() {
			return {
				searchForm: {
					key: ""
				},
				sessionTable: "", //
				role: "", //
				userId: "", //id
				//
				shangpinTypesSelectSearch: [],
				//
				shangpinOrderTypesSelectSearch: [],
				form: {
					id: null,
					shangpinOrderUuidNumber: null,
					addressId: null,
					shangpinId: null,
					buyNumber: null,
					shangpinOrderTruePrice: null,
					caigouyuanId: null,
					yonghuId: null,
					shangpinOrderTypes: null,
					shangpinOrderPaymentTypes: null,
					insertTime: null,
					createTime: null,
				},
				dataList: [],
				pageIndex: 1,
				pageSize: 10,
				totalPage: 0,
				dataListLoading: false,
				dataListSelections: [],
				showFlag: true,
				sfshVisiable: false,
				shForm: {},
				chartVisiable: false,
				echartsDate: new Date(), //echarts
				addOrUpdateFlag: false,
				contents: null,
				layouts: '',

				fahuoDate: {
					id: null,
					caigouyuanId: null
				},
				caigouyuanSelectSearch: [],
				dialogFormVisible: false,

				//excel
				json_fields: {
					//
					'': 'addressName',
					'': 'addressPhone',
					'': 'addressDizhi',
					'': 'isdefaultTypes',
					'': 'caigouyuanName',
					'': 'caigouyuanPhone',
					'': 'caigouyuanIdNumber',
					'': 'caigouyuanPhoto',
					'': 'caigouyuanEmail',
					'': 'shangpinName',
					'': 'shangpinUuidNumber',
					'': 'shangpinPhoto',
					'': 'shangpinTypes',
					'': 'shangpinKucunNumber',
					'': 'shangpinOldMoney',
					'': 'shangpinNewMoney',
					'': 'shangpinClicknum',
					'': 'shangxiaTypes',
					'': 'yonghuName',
					'': 'yonghuPhone',
					'': 'yonghuIdNumber',
					'': 'yonghuPhoto',
					'': 'yonghuEmail',
					'': 'newMoney',
					//
					'': "shangpinOrderUuidNumber",
					'': "buyNumber",
					'': "shangpinOrderTruePrice",
					'': "shangpinOrderTypes",
					'': "shangpinOrderPaymentTypes",
					'': "insertTime",
				},

				//
				//id
				commentbackId: null,
				//
				commentbackContent: null,
				//
				commentbackVisible: false,
				//
				shangpinCommentbackPingfenNumber: 0,
			};
		},
		created() {
			this.contents = styleJs.listStyle();
			this.init();
			this.getDataList();
			this.contentStyleChange()
		},
		mounted() {
			//
			this.sessionTable = this.$storage.get("sessionTable");
			this.role = this.$storage.get("role");
			this.userId = this.$storage.get("userId");

		},
		filters: {
			htmlfilter: function(val) {
				return val.replace(/<[^>]*>/g).replace(/undefined/g, '');
			}
		},
		components: {
			AddOrUpdate,
		},
		computed: {
			getActionUrl: function() {
				return `/` + this.$base.name + `/file/upload`;
			}
		},
		methods: {
			chartDialog() {
				let _this = this;
				let params = {
					dateFormat: "%Y", //%Y-%m
					riqi: _this.echartsDate.getFullYear(),
					// riqi :_this.echartsDate.getFullYear()+"-"+(_this.echartsDate.getMonth() + 1 < 10 ? '0' + (_this.echartsDate.getMonth() + 1) : _this.echartsDate.getMonth() + 1),
					thisTable: { //
						tableName: 'shangpin_order', //,
						sumColum: 'shangpin_order_number', //
						date: 'insert_time', //
						// string : 'shangpin_order_name',//
						// types : 'shangpin_order_types',//
					},
					// joinTable : {//
					//     tableName :'yonghu',//
					//     // date : 'insert_time',//
					//     string : 'yonghu_name',//
					//     // types : 'yonghu_types',//
					// }
				}
				_this.chartVisiable = true;
				_this.$nextTick(() => {
					var statistic = this.$echarts.init(document.getElementById("statistic"), 'macarons');
					this.$http({
						url: "barSum",
						method: "get",
						params: params
					}).then(({
						data
					}) => {
						if (data && data.code === 0) {

							//  
							//start
							let yAxisName = ""; //(,)
							let xAxisName = "";
							let series = []; //
							data.data.yAxis.forEach(function(item, index) {
								let tempMap = {};
								// tempMap.name= [""];//(,)
								tempMap.name = data.data.legend[index];
								tempMap.type = 'bar';
								tempMap.data = item;
								series.push(tempMap);

							})

							var option = {
								tooltip: {
									trigger: 'axis',
									axisPointer: {
										type: 'cross',
										crossStyle: {
											color: '#999'
										}
									}
								},
								toolbox: {
									feature: {
										// dataView: { show: true, readOnly: false },  // 
										magicType: {
											show: true,
											type: ['line', 'bar']
										}, //
										// restore: { show: true }, // 
										saveAsImage: {
											show: true
										} //
									}
								},
								legend: {
									data: data.data.legend //  
								},
								xAxis: [{
									type: 'category',
									axisLabel: {
										interval: 0
									},
									name: xAxisName,
									data: data.data.xAxis,
									axisPointer: {
										type: 'shadow'
									}
								}],
								yAxis: [{
									type: 'value', //
									name: yAxisName, //y
									axisLabel: {
										formatter: '{value}' // 
									}
								}],
								series: series //
							};
							// 
							statistic.setOption(option, true);
							//
							window.onresize = function() {
								statistic.resize();
							};
							//end
						} else {
							this.$message({
								message: "",
								type: "success",
								duration: 1500,
								onClose: () => {
									this.search();
								}
							});
						}
					});
				});
				////
				//_this.chartVisiable = true;
				// this.$nextTick(()=>{
				//     var statistic = this.$echarts.init(document.getElementById("statistic"),'macarons');
				//     let params = {
				//         tableName: "shangpin_order",
				//         groupColumn: "shangpin_order_types",
				//     }
				//     this.$http({
				//         url: "newSelectGroupCount",
				//         method: "get",
				//         params: params
				//     }).then(({data}) => {
				//         if (data && data.code === 0) {
				//             let res = data.data;
				//             let xAxis = [];
				//             let yAxis = [];
				//             let pArray = []
				//             var option = {};
				//             for(let i=0;i<res.length;i++){
				//                 xAxis.push(res[i].name);
				//                 yAxis.push(res[i].value);
				//                 pArray.push({
				//                     value: res[i].value,
				//                     name: res[i].name
				//                 })
				//                 option = {
				//                     title: {
				//                         text: '',
				//                         left: 'center'
				//                     },
				//                     tooltip: {
				//                         trigger: 'item',
				//                         formatter: '{b} : {c} ({d}%)'
				//                     },
				//                     series: [
				//                         {
				//                             type: 'pie',
				//                             radius: '55%',
				//                             center: ['50%', '60%'],
				//                             data: pArray,
				//                             emphasis: {
				//                                 itemStyle: {
				//                                     shadowBlur: 10,
				//                                     shadowOffsetX: 0,
				//                                     shadowColor: 'rgba(0, 0, 0, 0.5)'
				//                                 }
				//                             }
				//                         }
				//                     ]
				//                 };
				//             }
				//                 statistic.setOption(option);
				//                 window.onresize = function() {
				//                     statistic.resize();
				//                 };
				//         }
				//     });
				// })
			},
			contentStyleChange() {
				this.contentSearchStyleChange()
				this.contentBtnAdAllStyleChange()
				this.contentSearchBtnStyleChange()
				this.contentTableBtnStyleChange()
				this.contentPageStyleChange()
			},
			contentSearchStyleChange() {
				this.$nextTick(() => {
					document.querySelectorAll('.form-content .slt .el-input__inner').forEach(el => {
						let textAlign = 'left'
						if (this.contents.inputFontPosition == 2)
							textAlign = 'center'
						if (this.contents.inputFontPosition == 3) textAlign = 'right'
						el.style.textAlign = textAlign
						el.style.height = this.contents.inputHeight
						el.style.lineHeight = this.contents.inputHeight
						el.style.color = this.contents.inputFontColor
						el.style.fontSize = this.contents.inputFontSize
						el.style.borderWidth = this.contents.inputBorderWidth
						el.style.borderStyle = this.contents.inputBorderStyle
						el.style.borderColor = this.contents.inputBorderColor
						el.style.borderRadius = this.contents.inputBorderRadius
						el.style.backgroundColor = this.contents.inputBgColor
					})
					if (this.contents.inputTitle) {
						document.querySelectorAll('.form-content .slt .el-form-item__label').forEach(el => {
							el.style.color = this.contents.inputTitleColor
							el.style.fontSize = this.contents.inputTitleSize
							el.style.lineHeight = this.contents.inputHeight
						})
					}
					setTimeout(() => {
						document.querySelectorAll('.form-content .slt .el-input__prefix').forEach(el => {
							el.style.color = this.contents.inputIconColor
							el.style.lineHeight = this.contents.inputHeight
						})
						document.querySelectorAll('.form-content .slt .el-input__suffix').forEach(el => {
							el.style.color = this.contents.inputIconColor
							el.style.lineHeight = this.contents.inputHeight
						})
						document.querySelectorAll('.form-content .slt .el-input__icon').forEach(el => {
							el.style.lineHeight = this.contents.inputHeight
						})
					}, 10)
				})
			},
			// 
			contentSearchBtnStyleChange() {
				this.$nextTick(() => {
					document.querySelectorAll('.form-content .slt .el-button--success').forEach(el => {
						el.style.height = this.contents.searchBtnHeight
						el.style.color = this.contents.searchBtnFontColor
						el.style.fontSize = this.contents.searchBtnFontSize
						el.style.borderWidth = this.contents.searchBtnBorderWidth
						el.style.borderStyle = this.contents.searchBtnBorderStyle
						el.style.borderColor = this.contents.searchBtnBorderColor
						el.style.borderRadius = this.contents.searchBtnBorderRadius
						el.style.backgroundColor = this.contents.searchBtnBgColor
					})
				})
			},
			// 
			contentBtnAdAllStyleChange() {
				this.$nextTick(() => {
					document.querySelectorAll('.form-content .ad .el-button--success').forEach(el => {
						el.style.height = this.contents.btnAdAllHeight
						el.style.color = this.contents.btnAdAllAddFontColor
						el.style.fontSize = this.contents.btnAdAllFontSize
						el.style.borderWidth = this.contents.btnAdAllBorderWidth
						el.style.borderStyle = this.contents.btnAdAllBorderStyle
						el.style.borderColor = this.contents.btnAdAllBorderColor
						el.style.borderRadius = this.contents.btnAdAllBorderRadius
						el.style.backgroundColor = this.contents.btnAdAllAddBgColor
					})
					document.querySelectorAll('.form-content .ad .el-button--danger').forEach(el => {
						el.style.height = this.contents.btnAdAllHeight
						el.style.color = this.contents.btnAdAllDelFontColor
						el.style.fontSize = this.contents.btnAdAllFontSize
						el.style.borderWidth = this.contents.btnAdAllBorderWidth
						el.style.borderStyle = this.contents.btnAdAllBorderStyle
						el.style.borderColor = this.contents.btnAdAllBorderColor
						el.style.borderRadius = this.contents.btnAdAllBorderRadius
						el.style.backgroundColor = this.contents.btnAdAllDelBgColor
					})
					document.querySelectorAll('.form-content .ad .el-button--warning').forEach(el => {
						el.style.height = this.contents.btnAdAllHeight
						el.style.color = this.contents.btnAdAllWarnFontColor
						el.style.fontSize = this.contents.btnAdAllFontSize
						el.style.borderWidth = this.contents.btnAdAllBorderWidth
						el.style.borderStyle = this.contents.btnAdAllBorderStyle
						el.style.borderColor = this.contents.btnAdAllBorderColor
						el.style.borderRadius = this.contents.btnAdAllBorderRadius
						el.style.backgroundColor = this.contents.btnAdAllWarnBgColor
					})
				})
			},
			// 
			rowStyle({
				row,
				rowIndex
			}) {
				if (rowIndex % 2 == 1) {
					if (this.contents.tableStripe) {
						return {
							color: this.contents.tableStripeFontColor
						}
					}
				} else {
					return ''
				}
			},
			cellStyle({
				row,
				rowIndex
			}) {
				if (rowIndex % 2 == 1) {
					if (this.contents.tableStripe) {
						return {
							backgroundColor: this.contents.tableStripeBgColor
						}
					}
				} else {
					return ''
				}
			},
			headerRowStyle({
				row,
				rowIndex
			}) {
				return {
					color: this.contents.tableHeaderFontColor
				}
			},
			headerCellStyle({
				row,
				rowIndex
			}) {
				return {
					backgroundColor: this.contents.tableHeaderBgColor
				}
			},
			// 
			contentTableBtnStyleChange() {
				// this.$nextTick(()=>{
				//   setTimeout(()=>{
				//     document.querySelectorAll('.table-content .tables .el-table__body .el-button--success').forEach(el=>{
				//       el.style.height = this.contents.tableBtnHeight
				//       el.style.color = this.contents.tableBtnDetailFontColor
				//       el.style.fontSize = this.contents.tableBtnFontSize
				//       el.style.borderWidth = this.contents.tableBtnBorderWidth
				//       el.style.borderStyle = this.contents.tableBtnBorderStyle
				//       el.style.borderColor = this.contents.tableBtnBorderColor
				//       el.style.borderRadius = this.contents.tableBtnBorderRadius
				//       el.style.backgroundColor = this.contents.tableBtnDetailBgColor
				//     })
				//     document.querySelectorAll('.table-content .tables .el-table__body .el-button--primary').forEach(el=>{
				//       el.style.height = this.contents.tableBtnHeight
				//       el.style.color = this.contents.tableBtnEditFontColor
				//       el.style.fontSize = this.contents.tableBtnFontSize
				//       el.style.borderWidth = this.contents.tableBtnBorderWidth
				//       el.style.borderStyle = this.contents.tableBtnBorderStyle
				//       el.style.borderColor = this.contents.tableBtnBorderColor
				//       el.style.borderRadius = this.contents.tableBtnBorderRadius
				//       el.style.backgroundColor = this.contents.tableBtnEditBgColor
				//     })
				//     document.querySelectorAll('.table-content .tables .el-table__body .el-button--danger').forEach(el=>{
				//       el.style.height = this.contents.tableBtnHeight
				//       el.style.color = this.contents.tableBtnDelFontColor
				//       el.style.fontSize = this.contents.tableBtnFontSize
				//       el.style.borderWidth = this.contents.tableBtnBorderWidth
				//       el.style.borderStyle = this.contents.tableBtnBorderStyle
				//       el.style.borderColor = this.contents.tableBtnBorderColor
				//       el.style.borderRadius = this.contents.tableBtnBorderRadius
				//       el.style.backgroundColor = this.contents.tableBtnDelBgColor
				//     })

				//   }, 50)
				// })
			},
			// 
			contentPageStyleChange() {
				let arr = []
				if (this.contents.pageTotal) arr.push('total')
				if (this.contents.pageSizes) arr.push('sizes')
				if (this.contents.pagePrevNext) {
					arr.push('prev')
					if (this.contents.pagePager) arr.push('pager')
					arr.push('next')
				}
				if (this.contents.pageJumper) arr.push('jumper')
				this.layouts = arr.join()
				this.contents.pageEachNum = 10
			},

			init() {},
			search() {
				this.pageIndex = 1;
				this.getDataList();
			},
			// 
			getDataList() {
				this.dataListLoading = true;
				let params = {
					page: this.pageIndex,
					limit: this.pageSize,
					sort: 'id',
				}


				if (this.searchForm.caigouyuanName != '' && this.searchForm.caigouyuanName != undefined) {
					params['caigouyuanName'] = '%' + this.searchForm.caigouyuanName + '%'
				}

				if (this.searchForm.shangpinName != '' && this.searchForm.shangpinName != undefined) {
					params['shangpinName'] = '%' + this.searchForm.shangpinName + '%'
				}

				if (this.searchForm.shangpinUuidNumber != '' && this.searchForm.shangpinUuidNumber != undefined) {
					params['shangpinUuidNumber'] = '%' + this.searchForm.shangpinUuidNumber + '%'
				}

				if (this.searchForm.shangpinTypes != '' && this.searchForm.shangpinTypes != undefined) {
					params['shangpinTypes'] = this.searchForm.shangpinTypes
				}

				if (this.searchForm.yonghuName != '' && this.searchForm.yonghuName != undefined) {
					params['yonghuName'] = '%' + this.searchForm.yonghuName + '%'
				}

				if (this.searchForm.shangpinOrderUuidNumber != '' && this.searchForm.shangpinOrderUuidNumber != undefined) {
					params['shangpinOrderUuidNumber'] = '%' + this.searchForm.shangpinOrderUuidNumber + '%'
				}

				if (this.searchForm.shangpinOrderTypes != '' && this.searchForm.shangpinOrderTypes != undefined) {
					params['shangpinOrderTypes'] = this.searchForm.shangpinOrderTypes
				}

				params['shangpinOrderDelete'] = 1 //  1  2 


				this.$http({
					url: "shangpinOrder/page",
					method: "get",
					params: params
				}).then(({
					data
				}) => {
					if (data && data.code === 0) {
						this.dataList = data.data.list;
						this.totalPage = data.data.total;
					} else {
						this.dataList = [];
						this.totalPage = 0;
					}
					this.dataListLoading = false;
				});

				//
				this.$http({
					url: "dictionary/page?dicCode=shangpin_types&page=1&limit=100",
					method: "get",
					page: 1,
					limit: 100,
				}).then(({
					data
				}) => {
					if (data && data.code === 0) {
						this.shangpinTypesSelectSearch = data.data.list;
					}
				});
				this.$http({
					url: "caigouyuan/list?page=1&limit=100",
					method: "get",
					page: 1,
					limit: 100,
				}).then(({
					data
				}) => {
					if (data && data.code === 0) {
						this.caigouyuanSelectSearch = data.data.list;
					}
				});
				//
				//
				this.$http({
					url: "dictionary/page?dicCode=shangpin_order_types&page=1&limit=100",
					method: "get",
				}).then(({
					data
				}) => {
					if (data && data.code === 0) {
						this.shangpinOrderTypesSelectSearch = data.data.list;
					}
				});
			},
			//
			sizeChangeHandle(val) {
				this.pageSize = val;
				this.pageIndex = 1;
				this.getDataList();
			},
			// 
			currentChangeHandle(val) {
				this.pageIndex = val;
				this.getDataList();
			},
			// 
			selectionChangeHandler(val) {
				this.dataListSelections = val;
			},
			// /
			addOrUpdateHandler(id, type) {
				this.showFlag = false;
				this.addOrUpdateFlag = true;
				this.crossAddOrUpdateFlag = false;
				if (type != 'info') {
					type = 'else';
				}
				this.$nextTick(() => {
					this.$refs.addOrUpdate.init(id, type);
				});
			},
			// 
			download(file) {
				window.open(" ${file} ")
			},
			// 
			deleteHandler(id) {
				var ids = id ? [Number(id)] : this.dataListSelections.map(item => {
					return Number(item.id);
				});

				this.$confirm(`[${id ? "" : ""}]?`, "", {
					confirmButtonText: "",
					cancelButtonText: "",
					type: "warning"
				}).then(() => {
					this.$http({
						url: "shangpinOrder/delete",
						method: "post",
						data: ids
					}).then(({
						data
					}) => {
						if (data && data.code === 0) {
							this.$message({
								message: "",
								type: "success",
								duration: 1500,
								onClose: () => {
									this.search();
								}
							});
						} else {
							this.$message.error(data.msg);
						}
					});
				});
			},
			// 
			shangpinOrderUploadSuccess(data) {
				let _this = this;
				_this.$http({
					url: "shangpinOrder/batchInsert?fileName=" + data.file,
					method: "get"
				}).then(({
					data
				}) => {
					if (data && data.code === 0) {
						_this.$message({
							message: "",
							type: "success",
							duration: 1500,
							onClose: () => {
								_this.search();
							}
						});
					} else {
						_this.$message.error(data.msg);
					}
				});

			},
			// 
			shangpinOrderUploadError(data) {
				this.$message.error('');
			},
			//
			refund(id) {
				this.$confirm(`?`, "", {
					confirmButtonText: "",
					cancelButtonText: "",
					type: "warning"
				}).then(() => {
					this.$http({
						url: "shangpinOrder/refund?id=" + id,
						method: "post",
					}).then(({
						data
					}) => {
						if (data && data.code === 0) {
							this.$message({
								message: "",
								type: "success",
								duration: 1500,
								onClose: () => {
									this.search();
								}
							});
						} else {
							this.$message.error(data.msg);
						}
					});
				});
			},
			opdenDeliver(id) {
				this.fahuoDate.caigouyuanId = null
				this.fahuoDate.id = null
				this.fahuoDate.id = id
				this.dialogFormVisible = true
			},
			//
			deliver() {
				let _this = this;
				let data = {
					id: this.fahuoDate.id,
					shangpinOrderTypes: 3,
					caigouyuanId: this.fahuoDate.caigouyuanId
				}
				this.$confirm(`?`, "", {
					confirmButtonText: "",
					cancelButtonText: "",
					type: "warning"
				}).then(() => {
					this.$http({
						url: "shangpinOrder/update",
						method: "post",
						data
					}).then(({
						data
					}) => {
						if (data && data.code === 0) {
							this.$message({
								message: "",
								type: "success",
								duration: 1500,
								onClose: () => {
									this.search();
									_this.dialogFormVisible = false
								}
							});
						} else {
							_this.$message.error(data.msg);
							_this.dialogFormVisible = false
						}
					});
				});
			},







			//
			receiving(id) {
				this.$confirm(`?`, "", {
					confirmButtonText: "",
					cancelButtonText: "",
					type: "warning"
				}).then(() => {
					this.$http({
						url: "shangpinOrder/receiving?id=" + id,
						method: "post",
					}).then(({
						data
					}) => {
						if (data && data.code === 0) {
							this.$message({
								message: "",
								type: "success",
								duration: 1500,
								onClose: () => {
									this.search();
								}
							});
						} else {
							this.$message.error(data.msg);
						}
					});
				});
			},





			//
			openCommentModel(id) {
				//id
				this.commentbackId = null
				//
				this.commentbackContent = null
				//
				this.commentbackId = id
				//
				this.commentbackVisible = true
			},
			//
			commentback(id) {
				var _this = this
				if (this.commentbackContent == null || this.commentbackContent == 0) {
					this.$message.error("");
					return false;
				}
				this.$confirm(`?`, "", {
					confirmButtonText: "",
					cancelButtonText: "",
					type: "warning"
				}).then(() => {
					this.$http({
						url: "shangpinOrder/commentback?id=" + _this.commentbackId + "&commentbackText=" + _this.commentbackContent +
							"&shangpinCommentbackPingfenNumber=" + _this.shangpinCommentbackPingfenNumber,
						method: "post",
					}).then(({
						data
					}) => {
						if (data && data.code === 0) {
							this.$message({
								message: "",
								type: "success",
								duration: 1500,
								onClose: () => {
									_this.search();
									//id
									_this.commentbackId = null
									//
									_this.commentbackContent = null
									//
									_this.commentbackVisible = false
									//
									_this.shangpinCommentbackPingfenNumber = null
								}
							});
						} else {
							//id
							_this.commentbackId = null,
								//
								_this.commentbackContent = null,
								//
								_this.commentbackVisible = false,
								//
								_this.shangpinCommentbackPingfenNumber = null
							this.$message.error(data.msg);
						}
					});
				});
			},




		}
	};
</script>
<style lang="scss" scoped>
	.slt {
		margin: 0 !important;
		display: flex;
	}

	.ad {
		margin: 0 !important;
		display: flex;
	}

	.pages {
		& /deep/ el-pagination__sizes {
			& /deep/ el-input__inner {
				height: 22px;
				line-height: 22px;
			}
		}
	}


	.el-button+.el-button {
		margin: 0;
	}

	.tables {
		& /deep/ .el-button--success {
			height: 35px;
			color: rgba(4, 4, 4, 1);
			font-size: 1px;
			border-width: 0px;
			border-style: solid;
			border-color: rgba(1, 1, 1, 1);
			border-radius: 4px;
			background-color: rgba(253, 228, 79, 1);
		}

		& /deep/ .el-button--primary {
			height: 35px;
			color: rgba(8, 8, 8, 1);
			font-size: 1px;
			border-width: 0px;
			border-style: solid;
			border-color: rgba(1, 1, 1, 1);
			border-radius: 4px;
			background-color: rgba(240, 125, 48, 1);
		}

		& /deep/ .el-button--danger {
			height: 35px;
			color: rgba(8, 8, 8, 1);
			font-size: 1px;
			border-width: 0px;
			border-style: solid;
			border-color: rgba(1, 1, 1, 1);
			border-radius: 4px;
			background-color: rgba(233, 95, 64, 1);
		}

		& /deep/ .el-button {
			margin: 4px;
		}
	}
</style>
